  props: {
    item: Object
  },
  data() {
	  return {
	  	popoverVisible: false,
		depthValue: 1
	  }
  },
  computed: {
  },
  methods: {
  	depthClick(e) {
		this.popoverVisible = !this.popoverVisible;
  	},
	depthChange(e) {
		this.depthValue = e;
		this.$emit('depthChange', e);
	}
  }
}
